<template>
    <div class="container">
        <button class="menu-toggle">â˜°</button>
        <div class="sidebar">
            <h2>Race Options</h2>
            <h3>Performance Points:</h3>
            <p>Min</p><input type="number" v-model="performancePointsMin" />
            <p>Max</p><input type="number" v-model="performancePointsMax" />
            <h3>Year Range:</h3>
            <p>Min</p><input type="number" v-model="yearMin" />
            <p>Max</p><input type="number" v-model="yearMax" />
            <p>Radius</p><input type="number" v-model="yearRadius" />   
            <button class="button-race" @click="generateRace">Race!</button>
            <div v-if="validationErrors.length">
                <p v-for="(error, index) in validationErrors" :key="index">
                    {{ error }}
                </p>
            </div>
        </div>
        <div class="main-content">
            <div class="content-header">
                <img id="gt-logo" alt="GTRaceNight" src="../assets/GTRaceNightLogo.webp">
                <h1>GT RACE NIGHT!</h1>
            </div>
            
            <div v-if="raceGenerated">
                <h1>{{ selectedTrack }}</h1>
                <h1>Performance Points: {{ selectedPerformancePoint }}</h1>
                <h1>Year Range ({{ selectedYear }}): {{ selectedYear - yearRadius }} - {{ selectedYear + yearRadius }}</h1>
            </div>

            <div class="history-table" v-if="history.length">
                <table class="table-centered">
                    <thead>
                        <tr>
                            <th>Track</th>
                            <th>PP</th>
                            <th>Year</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(race, index) in history" :key="index">
                            <td> {{ race.track }} </td>
                            <td> {{ race.pp }} </td>
                            <td> {{ race.year }} </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            
        </div>
    </div>
</template>
  
<script>
export default {
    name: 'RaceNight',
    data() {
        return {
            performancePointsMin: 550,
            performancePointsMax: 1000,
            yearMin: 1965,
            yearMax: 2024,
            yearRadius: 2,
            raceGenerated: false,
            validationErrors: [],
            selectedTrack: '',
            selectedPerformancePoint: 0,
            selectedYear: 0,
            history: [],
            tracks: [
                "24 Heures du Mans Racing Circuit Full Course",
                "24 Heures du Mans Racing Circuit No Chicane",
                "Alsace Test Course",
                "Alsace Test Course Reverse",
                "Alsace Village",
                "Alsace Village Reverse",
                "Autodrome Lago Maggiore Centre",
                "Autodrome Lago Maggiore Centre Reverse",
                "Autodrome Lago Maggiore East",
                "Autodrome Lago Maggiore East End",
                "Autodrome Lago Maggiore East End Reverse",
                "Autodrome Lago Maggiore East Reverse",
                "Autodrome Lago Maggiore Full Course",
                "Autodrome Lago Maggiore Full Course Reverse",
                "Autodrome Lago Maggiore West",
                "Autodrome Lago Maggiore West End",
                "Autodrome Lago Maggiore West End Reverse",
                "Autodrome Lago Maggiore West Reverse",
                "Autodromo de Interlagos Full Course",
                "Autodromo Nazionale Monza Full Course",
                "Autodromo Nazionale Monza No Chicane",
                "Autopolis Full Course",
                "Autopolis Shortcut Circuit",
                "Blue Moon Bay Speedway Full Course",
                "Blue Moon Bay Speedway Full Course Reverse",
                "Blue Moon Bay Speedway Infield A",
                "Blue Moon Bay Speedway Infield A Reverse",
                "Blue Moon Bay Speedway Infield B",
                "Blue Moon Bay Speedway Infield B Reverse",
                "Brands Hatch Grand Prix Circuit",
                "Brands Hatch Indy Circuit",
                "Broad Bean Raceway Full Course",
                "Broad Bean Raceway Full Course Reverse",
                "Circuit de Barcelona-Catalunya Grand Prix Layout",
                "Circuit de Barcelona-Catalunya National Layout",
                "Circuit de Barcelona-Catalunya No Chicane",
                "Circuit de Barcelona-Catalunya Rallycross Layout (Dirt)",
                "Circuit de Sainte-Croix Layout A",
                "Circuit de Sainte-Croix Layout A Reverse",
                "Circuit de Sainte-Croix Layout B",
                "Circuit de Sainte-Croix Layout B Reverse",
                "Circuit de Sainte-Croix Layout C",
                "Circuit de Sainte-Croix Layout C Reverse",
                "Circuit de Spa-Francorchamps 24h Layout",
                "Circuit de Spa-Francorchamps Full Course",
                "Colorado Springs Lake (Dirt)",
                "Colorado Springs Lake Reverse (Dirt)",
                "Daytona International Speedway Road Course",
                "Daytona International Speedway Tri-Oval",
                "Deep Forest Full Course",
                "Deep Forest Full Course Reverse",
                "Dragon Trail Gardens",
                "Dragon Trail Gardens Reverse",
                "Dragon Trail Seaside",
                "Dragon Trail Seaside Reverse",
                "Fishermans Ranch Full Course (Dirt)",
                "Fishermans Ranch Full Course Reverse (Dirt)",
                "Fuji International Speedway Full Course",
                "Fuji International Speedway Short Course",
                "Goodwood Full Course",
                "Grand Valley - Highway 1",
                "Grand Valley - Highway 1 Reverse",
                "Grand Valley - South",
                "Grand Valley - South Reverse",
                "High Speed Ring Full Course",
                "High Speed Ring Full Course Reverse",
                "Kyoto Driving Park Miyabi",
                "Kyoto Driving Park Yamagiwa",
                "Kyoto Driving Park Yamagiwa + Miyabi",
                "Kyoto Driving Park Yamagiwa + Miyabi Reverse",
                "Kyoto Driving Park Yamagiwa Reverse",
                "Michelin Raceway Road Atlanta",
                "Mount Panorama Circuit Full Course",
                "Northern Isle Speedway Full Course",
                "Nurburgring 24h Layout",
                "Nurburgring Grand Prix Track",
                "Nurburgring Nordschleife",
                "Red Bull Ring Full Course",
                "Red Bull Ring Short Track",
                "Sardegna - Road Track Layout A",
                "Sardegna - Road Track Layout A Reverse",
                "Sardegna - Road Track Layout B",
                "Sardegna - Road Track Layout B Reverse",
                "Sardegna - Road Track Layout C",
                "Sardegna - Road Track Layout C Reverse",
                "Sardegna - Windmills Full Course (Dirt)",
                "Sardegna - Windmills Full Course Reverse (Dirt)",
                "Special Stage Route X Full Course",
                "Suzuka Circuit East Course",
                "Suzuka Circuit Full Course",
                "Tokyo Expressway Central Clockwise",
                "Tokyo Expressway Central Counterclockwise",
                "Tokyo Expressway East Clockwise",
                "Tokyo Expressway East Counterclockwise",
                "Tokyo Expressway South Clockwise",
                "Tokyo Expressway South Counterclockwise",
                "Trial Mountain Full Course",
                "Trial Mountain Full Course Reverse",
                "Tsukuba Circuit Full Course",
                "Watkins Glen International Long Course",
                "Watkins Glen International Short Course",
                "WeatherTech Raceway Laguna Seca Full Course",
                "Willow Springs International Big Willow",
                "Willow Springs International Horse Thief Mile",
                "Willow Springs International Horse Thief Mile Reverse",
                "Willow Springs International Streets of Willow Springs",
                "Willow Springs International Streets of Willow Springs Reverse"
            ]
        }
    },
    mounted() {
        
    },
    methods: {
        getRandomInt(min = 0, max = 100) {
            return Math.floor(Math.random() * (max - min + 1)) + min
        },
        generateRace() {
            this.raceGenerated = false;
            if(!this.formValidation()) {
                return;
            }
            this.selectedTrack = this.getTrack();
            this.selectedPerformancePoint = this.getPerformancePoint();
            this.selectedYear = this.getYear();
            this.history.push({
                track: this.selectedTrack,
                pp: this.selectedPerformancePoint,
                year: this.selectedYear
            })
            this.raceGenerated = true;
        },
        formValidation() {
            this.validationErrors = [];
            if(!Number.isInteger(this.performancePointsMin)) {
                this.validationErrors.push('PP Min is not a number!')
            }
            if(!Number.isInteger(this.performancePointsMax)) {
                this.validationErrors.push('PP Max is not a number!')
            }
            if(!Number.isInteger(this.yearMin)) {
                this.validationErrors.push('Year Min is not a number!')
            }
            if(!Number.isInteger(this.yearMax)) {
                this.validationErrors.push('Year Max is not a number!')
            }
            if(!Number.isInteger(this.yearRadius)) {
                this.validationErrors.push('Year Radius is not a number!')
            }
            if(this.performancePointsMin > this.performancePointsMax) {
                this.validationErrors.push('PP Min cannot be greater than PP Max')
            }
            if(this.yearMin > this.yearMax) {
                this.validationErrors.push('PP Min cannot be greater than PP Max')
            }
            return !this.validationErrors.length ? true : false;
        },
        getTrack() {
            return this.tracks[this.getRandomInt(0,this.tracks.length)];
        },
        getPerformancePoint() {
            return this.getRandomInt(this.performancePointsMin, this.performancePointsMax);
        },
        getYear() {
            return this.getRandomInt(this.yearMin, this.yearMax);
        }
    }

}
</script>
  
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
