    flex: 0 0 250px; /* Fixed width for the sidebar */
    background-color: #f4f4f4;
    padding: 20px;
    height: 100vh; /* Full height */
    box-sizing: border-box;
    text-align: left;
}

.sidebar input {
    width: 98%;
    padding: 5px;
    font-family: Avenir, Helvetica, Arial, sans-serif;
    font-size: large;
}

.main-content {
    flex-grow: 1; /* Takes up the remaining width */
    padding: 20px;
    background-color: #fff;
    box-sizing: border-box;
    text-align: center;
}

.menu-toggle {
    display: none; /* Hide by default */
    padding: 10px;
    font-size: 20px;
    cursor: pointer;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .container {
        flex-direction: column;
    }
    .sidebar {
        height: auto;
        flex: 0 0 50px; 
    }
    .main-content {
        height: auto;
    }

    /* might not need*/
    /*.menu-toggle {
        display: block; 
        position: absolute;
        top: 10px;
        left: 10px;
        z-index: 2;
    }*/
}
#gt-logo {
    height: 100px;
    border-radius: 20%;
    float: left;
}
.content-header {
    height: 100px;
    line-height: 100px;
}

.history-table { 
    height: 100vh; /* Make the container full height of the viewport */
    text-align: center; /* Ensure text inside the table is centered, if desired */
}

/* Table styling */
.table-centered {
    border-collapse: collapse; /* Optional: collapse borders */
    margin: 0 auto; /* Center the table horizontally */
}

.table-centered th,
.table-centered td {
    border: 1px solid #ddd; /* Optional: add border to table cells */
